<template>
    <card id="my-tag-selector" class="card">
      <div slot="title">标签选择器</div>
      <div v-if="isNotEmpty">
        <RadioGroup v-model="choiceTag" v-on:on-change="change" type="button" size="small">
          <Radio v-for="(tag,index) in tags" :key="index" :label="tag"></Radio>
        </RadioGroup>
      </div>
      <div v-else>
        当前无标签
      </div>
    </card>
</template>

<script>
export default {
  name: 'MyTagSelector',
  props: {
    tags: Array
  },
  data () {
    return {
      choiceTag: '全部'
    }
  },
  methods: {
    isNotEmpty () {
      return this.tags.length !== 0
    },
    change (value) {
      this.$emit('change', value)
    }
  },
  created () {
    if (this.isNotEmpty()) {
      this.tags.unshift('全部')
    }
  }
}
</script>

<style scoped>
  #my-tag-selector{
    margin: 10px 0 0 10px;
  }
</style>



